.theme-dark {
    @body-background: #ecf0f5;

// ------start-导航------
@nav-top-background: #242d3f; // top 导航
@nav-left-background: #242d3f; // left 导航
// ------end-导航--------

// ------start-内容卡片------
@card-content: #151a24;
// ------end-内容卡片--------

// ------start-文本------
@text-title: #ffffff; // 文本标题
@text-content: #d6d8d8; // 文本正文
@text-interaction: #4cd0ff; // 文本交互
// ------end-文本--------

// ------start-弹出层------
@modal-background: #323849; // 弹出层背景
@modal-border: #454545;
// ------end-弹出层--------

// ------start-tooltip------
@tooltip-background: rgba(0, 0, 0, 0.6); // tooltip背景
// ------end-tooltip--------

// ------start-表单form------
@form-bg: #0c0e13;
@form-border: #454545;
@form-hover-bg: #0c0e13; // 鼠标移入背景
@form-hover-border: #767676; // 鼠标移入边框 & 文本
@form-press-bg: #0c0e13; // 鼠标点击背景
@form-press-border: #27a0cb; // 鼠标点击边框
@form-error-bg: #0c0e13;
@form-error-border: #cd6363;
@form-disable-bg: #1c222c;
@form-disable-border: #454545;
// ------end-表单form--------

// ------start-表格table------
@table-title-bg: #111111;
@table-space-bg-first: #1a202b;
@table-space-bg-second: #151a24;
@table-hover-interaction: #213f4e;
// ------end-表格table--------
@import './dark';
// @import (multiple) './theme';
}
